// 
import request from '@/utils/request'

// 
export const getComment = params => {
  return request({
    method: 'GET',
    url: '/mp/v1_0/articles',
    params
  })
}
// 
export const updateComment = (params, data) => {
  return request({
    method: 'PUT',
    url: '/mp/v1_0/comments/status',
    params,
    data
  })
}
// 
export const getFans = params => {
  return request({
    method: 'GET',
    url: '/mp/v1_0/followers',
    params
  })
}
