<template>
  <div class='publish-container'>
    <el-card class="box-card">
        <div slot="header" class="clearfix">
            <el-breadcrumb separator="/">
                <el-breadcrumb-item :to="{ path: '/' }"></el-breadcrumb-item>
                <el-breadcrumb-item>{{$route.query.id ? '' : ''}}</el-breadcrumb-item>
            </el-breadcrumb>
        </div>
         <el-form ref="publish-article" :model="article" label-width="60px" :rules="formRules">
            <el-form-item label="" style="width:500px" prop="title">
                <el-input v-model="article.title" placeholder="" ></el-input>
            </el-form-item>
             <el-form-item label=""  prop="content">
                <!-- <el-input type="textarea" v-model="article.content"></el-input> -->
                <el-tiptap v-model="article.content" :extensions="extensions" height=500 placeholder=""></el-tiptap>
            </el-form-item>
             <el-form-item label="">
                <el-radio-group v-model="article.cover.type">
                <el-radio :label="1"></el-radio>
                <el-radio :label="3"></el-radio>
                <el-radio :label="0"></el-radio>
                <el-radio :label="-1"></el-radio>
                </el-radio-group>
                <template>
                  <upload-cover
                  v-for="(cover, index) in article.cover.type"
                  :key="index"
                  v-model="article.cover.images[index]"
                  >
                  <!-- <upload-cover
                  v-for="(cover, index) in article.cover.type"
                  :key="index"
                  :cover-image="article.cover.images[index]"
                  @update-cover="onUpdateCover(index,$event)"
                  > -->

                  </upload-cover>
                </template>
            </el-form-item>
            <el-form-item label="" prop="channel_id">
                <el-select v-model="article.channel_id"  placeholder="">
                <el-option
                :label="channel.name"
                :value="channel.id"
                v-for="(channel, index) in channels"
                :key="index">
                 </el-option>
                </el-select>
            </el-form-item>
            <el-form-item>
                 <el-button  v-if="$route.query.id" type="success" @click="onPublish(false)"></el-button>
                <el-button v-else type="primary" @click="onPublish(false)"></el-button>
                <el-button  @click="onPublish(true)">{{$route.query.id ? '' : ''}}</el-button>
            </el-form-item>
      </el-form>
    </el-card>
  </div>
</template>
<script>
import {
  getArticleChannels,
  addArticle,
  getAppointArticle,
  updateArticle
} from '@/api/article'
import { uploadImage } from '@/api/image'
import UploadCover from './components/upload-cover'
// import element-tiptap 
import 'element-tiptap/lib/index.css'

import {
  ElementTiptap,
  Doc,
  Text,
  Paragraph,
  Heading,
  Bold,
  Underline,
  Italic,
  Strike,
  Link,
  Iframe,
  CodeBlock,
  Blockquote,
  Image,
  ListItem,
  BulletList,
  OrderedList,
  TodoItem,
  TodoList,
  TextAlign,
  Indent,
  LineHeight,
  HorizontalRule,
  HardBreak,
  TrailingNode,
  History,
  Table,
  TableHeader,
  TableCell,
  TableRow,
  FormatClear,
  TextColor,
  TextHighlight,
  Preview,
  Print,
  Fullscreen,
  SelectAll,
  FontType
} from 'element-tiptap'
export default {
  name: 'PublishIndex',
  props: {},
  components: {
    'el-tiptap': ElementTiptap,
    UploadCover
  },
  data () {
    return {
      article: {
        title: '',
        content: '',
        cover: {
          type: 1,
          images: []
        },
        channel_id: null
      },
      channels: [],
      extensions: [
        new Doc(),
        new Text(),
        new Paragraph(),
        new Heading({ level: 5 }),
        new Bold({ bubble: true }), // 
        new Underline(),
        new Italic(),
        new Strike(),
        new ListItem(),
        new BulletList(),
        new OrderedList(),
        new Link(),
        new Iframe(),
        new Image({
          uploadRequest (file) {
            const fd = new FormData()
            fd.append('image', file)
            return uploadImage(fd).then(res => {
              return res.data.data.url
            })
          } //  Promise<url>
        }),
        new CodeBlock(),
        new Blockquote(),
        new TodoItem(),
        new TodoList(),
        new TextAlign(),
        new Indent(),
        new LineHeight(),
        new HorizontalRule(),
        new HardBreak(),
        new TrailingNode(),
        new History(),
        new Table(),
        new TableHeader(),
        new TableCell(),
        new TableRow(),
        new FormatClear(),
        new TextColor(),
        new TextHighlight(),
        new Preview(),
        new Print(),
        new SelectAll(),
        new FontType(),
        new Fullscreen()
      ],
      formRules: {
        title: [
          { required: true, message: '', trigger: 'blur' },
          { min: 5, max: 30, message: ' 5 30 ', trigger: 'blur' }
        ],
        content: [
          {
            validator: (rule, value, callback) => {
              if (value === '<p></p>') {
                callback(new Error(''))
              } else {
                callback()
              }
            }
          },
          { required: true, message: '', trigger: 'blur' }
        ],
        channel_id: [
          { required: true, message: '', trigger: 'change' }
        ]

      }
    }
  },
  computed: {},
  watch: {},
  methods: {
    // 
    getChannels () {
      getArticleChannels().then(res => {
        this.channels = res.data.data.channels
      })
    },
    // 
    onPublish (draft = false) {
      // id
      const articleId = this.$route.query.id
      this.$refs['publish-article'].validate(value => {
        if (value) {
          // id  
          if (articleId) {
            updateArticle(articleId, this.article, draft).then(res => {
              if (draft === false) {
                this.$message({
                  message: '',
                  type: 'success',
                  center: true
                })
              } else {
                this.$message({
                  message: '',
                  type: 'success',
                  center: true
                })
              }
              this.$router.push('/article')
            })
          } else {
            addArticle(this.article, draft).then(res => {
              if (draft === false) {
                this.$message({
                  message: '',
                  type: 'success',
                  center: true
                })
              } else {
                this.$message({
                  message: '',
                  type: 'success',
                  center: true
                })
              }
              this.$router.push('/article')
            })
          }
        }
      })
    },
    // 
    getAppointArticle () {
      getAppointArticle(this.$route.query.id).then(res => {
        this.article = res.data.data
      })
    },
    onUpdateCover (index, url) {
      this.article.cover.images[index] = url
    }
  },
  created () {
    this.getChannels()
    // id 
    if (this.$route.query.id) {
      this.getAppointArticle()
    }
  },
  mounted () {},
  beforeDestroy () {}
}
</script>
