<template>
  <div class="article-container">
    <!--  -->
    <el-card class="box-card head-crad">
        <div slot="header" class="clearfix">
            <!-- -->
            <el-breadcrumb separator="/">
                <el-breadcrumb-item :to="{ path: '/' }"></el-breadcrumb-item>
                <el-breadcrumb-item></el-breadcrumb-item>
            </el-breadcrumb>
        <!-- -->
        </div>
        <!--  -->
            <el-form ref="form" :model="form" label-width="80px" size="small">
            <el-form-item label="">
                <el-radio-group v-model="status">
                  <el-radio :label="null"></el-radio>
                <el-radio :label="0"></el-radio>
                <el-radio :label="1"></el-radio>
                <el-radio :label="2"></el-radio>
                <el-radio :label="3"></el-radio>
                <el-radio :label="4"></el-radio>
                </el-radio-group>
            </el-form-item>
            <el-form-item label="">
                <el-select v-model="channelId" placeholder="">
                <el-option label="" :value="null"></el-option>
                <el-option
                v-for="(channel,index) in channels"
                :key="index"
                :label="channel.name"
                :value="channel.id"
                 >
                 </el-option>
                </el-select>
            </el-form-item>
            <el-form-item label="">
             <el-date-picker
            v-model="rangeDate"
            type="datetimerange"
            start-placeholder=""
            end-placeholder=""
            :default-time="['12:00:00']"
            format="yyyy-MM-dd"
            value-format="yyyy-MM-dd"
            >
          </el-date-picker>
            </el-form-item>
            <el-form-item>
                <el-button type="primary" :disabled="loading" @click="getArticle(page=1)"></el-button>
            </el-form-item>
            </el-form>
            <!-- / -->
    </el-card>
    <!--  -->
    <el-card class="box-card">
        <div slot="header" class="clearfix">
            <span> {{totalCount}} </span>
        </div>
        <!--  -->
            <el-table
            :data="articles"
            style="width: 100%"
            size="small"
            v-loading="loading"
            >
            <el-table-column
            prop="date"
            label=""
            >
            <template slot-scope="scope">
              <el-image
              style="width: 100px; height: 100px"
              :src="scope.row.cover.images[0]"
              lazy>
              </el-image>
              <!-- <img
              v-if="scope.row.cover.images[0]"
              class="article-cover"
              :src="scope.row.cover.images[0]"
              alt="">
              <img
              v-else
              class="article-cover"
              src="./no-cover.gif"
              alt=""> -->
            </template>
            </el-table-column>
            <el-table-column
            prop="title"
            label=""
            >
            </el-table-column>
            <el-table-column
            label="">
             <template slot-scope="scope">
            <el-tag :type="articleStatus[scope.row.status].type">{{articleStatus[scope.row.status].text}}</el-tag>
            <!-- <el-tag v-else-if ="scope.row.status === 1" type="warning"></el-tag>
            <el-tag v-else-if  ="scope.row.status === 2" type="success"></el-tag>
            <el-tag v-else-if  ="scope.row.status === 3" type="danger"></el-tag>
            <el-tag v-else-if  ="scope.row.status === 4" type="info"></el-tag> -->
             </template>
            </el-table-column>
            <el-table-column
            prop="pubdate"
            label="">
            </el-table-column>
            <el-table-column
            label="">
            <template slot-scope="scope">
              <el-button
                size="small"
                circle
                plain
                icon="el-icon-edit"
                type="primary"
                @click="$router.push('/publish?id=' + scope.row.id)"
               ></el-button>
              <el-button
                size="small"
                circle
                type="danger"
                plain
                icon="el-icon-delete"
                @click = "onDeleteArticle(scope.row.id)"
               ></el-button>
             </template>
              </el-table-column>
              </el-table>
        <!-- / -->

        <!--  -->
            <el-pagination
                background
                layout="prev, pager, next"
                :total="totalCount"
                :page-size="pageSize"
                :disabled="loading"
                :current-page.sync="page"
                @current-change="currentChange">
            </el-pagination>
        <!-- / -->
    </el-card>
  </div>
</template>
<script>
import { getArticle, getArticleChannels, deleteArticle } from '@/api/article'
export default {
  name: 'ArticleIndex',
  props: {},
  components: {},
  data () {
    return {
      form: {
        name: '',
        region: '',
        date1: '',
        date2: '',
        delivery: false,
        type: [],
        resource: '',
        desc: '',
        value1: ''
      },
      articleStatus: [
        { text: '', type: '' },
        { text: '', type: 'warning' },
        { text: '', type: 'success' },
        { text: '', type: 'danger' },
        { text: '', type: 'info' }
      ],
      articles: [], // 
      totalCount: 0, // 
      pageSize: 10, //  
      status: null, // 
      channels: [], // 
      channelId: null, // 
      rangeDate: null, // 
      loading: true, // 
      page: 1 // 
    }
  },
  computed: {},
  watch: {},
  methods: {
    // 
    getArticle (page = 1) {
      this.loading = true
      getArticle({
        page: this.page,
        per_page: this.pageSize,
        status: this.status,
        channel_id: this.channelId,
        begin_pubdate: this.rangeDate ? this.rangeDate[0] : null, // 
        end_pubdate: this.rangeDate ? this.rangeDate[1] : null // 
      }).then(res => {
        this.loading = false
        const { results, total_count: TotalCount } = res.data.data
        this.articles = results
        this.totalCount = TotalCount
      })
    },
    // 
    currentChange (page) {
      this.page = page
      this.getArticle()
    },
    // 
    getChannels () {
      getArticleChannels().then(res => {
        this.channels = res.data.data.channels
      })
    },
    // 
    onDeleteArticle (articleId) {
      this.$confirm('', '', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning'
      }).then(() => {
        // 
        deleteArticle(articleId.toString()).then(res => {
          this.$message({
            message: '',
            type: 'success',
            center: true
          })
          this.getArticle(this.page)
        })
      }).catch(() => {
        this.$message({
          type: 'info',
          message: '',
          center: true
        })
      })
    }
  },
  created () {
    // 
    this.getChannels()
    this.getArticle()
  },
  mounted () { },
  beforeDestroy () { }
}
</script>
